@import "../tools/variables.scss";

.contact-page {
  padding: 7rem 0;
  background: $color-primary-back;
  min-height: calc(100vh - 14rem);

  .section-title {
    margin-bottom: 4rem;
    text-align: center;
    .underline {
      width: 10rem;
      height: 0.25rem;
      margin-bottom: 1.25rem;
      background: $color-primary;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .page-info {
    max-width: 900px;
    width: 90vw;
    margin: 0 auto;
    text-align: center;

    p {
      font-size: 1.3em;
      line-height: 2;

      .highlight {
        color: $color-primary;
        font-weight: bold;
        cursor: pointer;
        &:hover {
          color: $color-primary-light;
        }
      }
    }
  }
}

@media screen and (min-width: $desktop-width) {
  .contact-page {
    font-size: 1.5em;
  }
}
